.hustle-modal-three {

	.hustle-modal-body {

		section {

			.hustle-modal-image {

				&_fill,
				&_cover,
				&_contain {
					height: $hui-image-height-sm;
					overflow: hidden;
				}

				&_fill {
					@extend %hui-image-fit-fill;
				}

				&_contain {
					@extend %hui-image-fit-contain;
				}

				&_cover {
					@extend %hui-image-fit-cover;
				}

				&_none {
					@extend %hui-image-fit-full;
				}
			}

			article {
				padding: $hui-gutter-sm;
			}
		}

		footer {
			padding: $hui-gutter-sm;

			form + .hustle-gdpr-box {
				margin-top: $hui-gutter-sm;
			}
		}

		&:not(.hustle-size-small) {

			section {

				article {

					.hustle-modal-article {

						@include screen("small") {
							margin: auto 0;
						}
					}

					@include screen("small") {
						display: flex;
						flex: 1 0 auto;
						flex-direction: column;
						justify-content: center;
						padding: $hui-gutter;
					}
				}

				@include screen("small") {
					display: flex;
					flex: 1;
					flex-direction: column;
				}
			}

			footer {
                flex-wrap: wrap;

				form + .hustle-gdpr-box {

					@include screen("small") {
						margin-top: $hui-gutter;
					}
				}

				.hui-modal-footer-wrap {

					@include screen("small") {
						flex: 1;
						margin: auto 0;
					}
				}

				@include screen("small") {
					display: flex;
					flex: 0 0 320px;
					align-items: center;
					padding: $hui-gutter;
				}
			}

			@include screen("small") {
				display: flex;
			}
		}
	}
}
