.hustle-modal-cabriolet {

	.hustle-modal {

		&-header {
			position: relative;
			margin: 0;
			padding: 0;
			border: 0;

			&.hustle-modal-with-title + .hustle-modal-content {
				margin: $hui-gutter-sm 0 0;
			}
		}

		&-content {
			overflow: hidden;

			.hustle-modal-wrap--content {
				padding: $hui-gutter-sm;
			}
		}

		&-image {

			&_fill,
			&_cover,
			&_contain {
				height: $hui-image-height-sm;
			}
	
			&_fill {
				@extend %hui-image-fit-fill;
			}
	
			&_contain {
				@extend %hui-image-fit-contain;
			}
	
			&_cover {
				@extend %hui-image-fit-cover;
			}
	
			&_none {
				@extend %hui-image-fit-full;
			}
		}

		&-message {

			+ .hustle-modal-footer {
				margin-top: $hui-gutter-sm;
				padding-top: #{$hui-gutter-sm + $hui-module-border-width};
				border-top-width: $hui-module-border-width;
				border-top-style: $hui-module-border-style;
				border-top-color: $hui-module-border-color;
				box-shadow: inset $hui-module-box-shadow;
			}
		}

		&-footer {

			.hustle-modal-cta {

				+ .hustle-gdpr-box {
					margin-top: $hui-gutter-sm;
				}
			}
		}
	}

	.hustle-modal-body:not(.hustle-size-small) {

		.hustle-modal {

			&-header {

				.hustle-modal-close {

					&:not(:only-child) {

						@include screen("small") {
							position: absolute;
							top: #{($hui-module-title-line-height - $hui-close-size) / 2};
							right: 0;
						}
					}

					+ .hustle-modal-subtitle {

						@include screen("small") {
							padding-top: #{($hui-module-title-line-height - $hui-module-subtitle-line-height) / 2};
							padding-right: #{$hui-close-size + 10px};
							padding-bottom: #{($hui-module-title-line-height - $hui-module-subtitle-line-height) / 2};
						}
					}
				}

				.hustle-modal-title {

					@include screen("small") {
						padding-right: #{$hui-close-size + 10px};
					}
				}

				&.hustle-modal-with-title + .hustle-modal-content {

					@include screen("small") {
						margin-top: $hui-gutter;
					}
				}

				@include screen("small") {
					position: relative;
				}
			}

			&-content {

				.hustle-modal-wrap--content {

					@include screen("small") {
						display: flex;
						flex: 1;
						flex-direction: column;
						justify-content: center;
						padding: $hui-gutter;
					}
				}

				.hustle-modal-align {

					@include screen("small") {
						margin: auto 0;
					}
				}

				@include screen("small") {
					display: flex;
				}
			}

			&-image {

				&_fill,
				&_cover,
				&_contain {

					&:only-child {

						@include screen("small") {
							max-width: 100%;
							height: $hui-image-height;
							flex: 1;
						}
					}
		
					@include screen("small") {
						max-width: $hui-image-height;
						height: auto;
						flex: 0 0 $hui-image-height;
					}
				}
		
				&_none {

					&:only-child {

						@include screen("small") {
							max-width: 100%;
							flex: 1;
						}
					}
		
					@include screen("small") {
						max-width: 50%;
						flex: 0 0 auto;
					}
				}
			}

			&-message {

				+ .hustle-modal-footer {

					@include screen("small") {
						margin-top: $hui-gutter;
						padding-top: #{$hui-gutter + $hui-module-border-width};
					}
				}
			}

			&-footer {

				.hustle-modal-cta {

					+ .hustle-gdpr-box {

						@include screen("small") {
							margin-top: $hui-gutter;
						}
					}
				}
			}
		}
	}
}