.hustle-modal {
	
	.hustle-modal-article,
	.hustle-modal-message {

		h1, h2, h3, h4, h5, h6, p, ul, ol, li, blockquote {
			color: inherit;
			font-family: $font--opn;
		}

		h1, h2, h3, h4, h5, h6 {
			margin: 0.6em 0;
			padding: 0;
			font-weight: bold;
			letter-spacing: 0;
			text-transform: none;

			&:first-child {
				margin-top: 0;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		h1 {
			font-size: 30px;
			line-height: 1.6em;
		}

		h2 {
			font-size: 22px;
			line-height: 1.4em;
		}

		h3 {
			font-size: 20px;
			line-height: 1.4em;
		}

		h4 {
			font-size: 18px;
			line-height: 1.8em;
		}

		h5 {
			font-size: 16px;
			line-height: 2em;
		}

		h6 {
			font-size: 13px;
			line-height: 2.2em;
		}

		p, ul, ol, blockquote {
			margin: 1.2em 0;
			padding: 0;
			border: 0;

			&:first-child {
				margin-top: 0;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		p, li {
			font-size: 14px;
			line-height: 1.45em;

			i {
				font-style: italic;
			}

			b, strong {
				font-weight: bold;
			}
		}

		ul, ol {
			padding-left: 17px;
		}

		li {
			margin: 0.6em 0;

			&:first-child {
				margin-top: 0;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		a {

			&:hover,
			&:focus,
			&:active {
				outline: none;
				box-shadow: none;
			}
		}

		img {
			max-width: 100%;
			height: auto;
			float: none;
			display: block;

			.alignleft {
				margin-left: 0;
				margin-right: auto;
			}

			&.aligncenter {
				margin: 0 auto;
			}

			&.alignright {
				margin-right: 0;
				margin-left: auto;
			}
		}

		blockquote {
			display: block;
			padding: 6px 10px 8px 20px;
			border-left-width: 4px;
			border-left-style: solid;
			background: rgba(0, 0, 0, 0.05);
		}

		iframe {
			max-width: 100%;
		}
	}
}
