.hustle-modal article {
	border-top: 0 !important; // Overwrite TwentySeventeen styles.

	hgroup {
		margin: 0 0 $hui-gutter-sm;
		padding: 0;
		border: 0;

		.hustle-modal-title,
		.hustle-modal-subtitle {
			margin: 0;
			padding: 0;
			border: 0;
		}

		.hustle-modal-title {
			font-size: $hui-optin-title-font-size;
			line-height: $hui-optin-title-line-height;
			font-family: $hui-optin-title-font-family;
			font-weight: $hui-optin-title-font-weight;

			+ .hustle-modal-subtitle {
				margin-top: 10px;
			}
		}

		.hustle-modal-subtitle {
			font-size: $hui-optin-subtitle-font-size;
			line-height: $hui-optin-subtitle-line-height;
			font-family: $hui-optin-subtitle-font-family;
			font-weight: $hui-optin-subtitle-font-weight;
		}

		&:last-child {
			margin: 0;
		}

		@include screen("small") {
			margin: 0 0 $hui-gutter;
		}
	}
}