.hustle-modal,
.wpmudev-ui .hustle-modal {

	&-close {
		width: $hui-close-size;
		height: $hui-close-size;
		display: block;
		margin-right: 0;
		margin-left: auto;

		.hustle-icon {
			width: $hui-close-size;
			height: $hui-close-size;
			cursor: pointer;
			display: block;
			margin: 0;
			padding: 9px;
		}
	}
}