.hustle-modal {

	.hustle-modal-optin {

		&_form {

			&.hustle-onload {

				.hustle-modal-optin_field {
					@include opacity(0.7);

					input {
						pointer-events: none;
					}
				}
			}
		}

		&_field {
			display: block;
			position: relative;
			margin-bottom: $hui-fields-joined;
			transition: $hui-transition;

			input {
				width: 100%;
				height: $hui-form-field-height;
				display: block;
				margin: 0;
				padding: $hui-form-field-padding;
				border-width: $hui-form-field-border-width;
				border-style: $hui-form-field-border-style;
				border-color: transparent;
				border-radius: 0;
				background-image: none;
				background-color: transparent;
				font-size: $hui-form-field-font-size;
				line-height: $hui-form-field-line-height;
				font-family: $hui-form-field-font-family;
				font-weight: $hui-form-field-font-weight;
				transition: $hui-transition;

				&,
				&:hover,
				&:focus {
					outline: none;
					box-shadow: none;
				}

				&:focus + label {

					.hustle-modal-optin_animated {
						opacity: 1;
						transform: translateX(0);
						transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
					}

					.hustle-modal-optin_placeholder {
						opacity: 0;
						transform: translateX(100px);
						transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
					}
				}

				&.hustle-modal-optin_error {
					border-color: $color-red !important;

					&:focus {
						border-color: transparent;
					}

					+ label .hustle-modal-optin_icon .hustle-i_warning path {
						fill: $color-red !important;
					}
				}
			}

			label {
				width: 100%;
				height: 100%;
				user-select: none;
				pointer-events: none;
				display: flex;
				flex-wrap: wrap;
				flex-direction: row;
				position: absolute;
				top: 0;
				left: 0;
				margin: 0;
				padding: $hui-form-field-padding;
				border-width: $hui-form-field-border-width;
				border-style: $hui-form-field-border-style;
				border-color: transparent;
				font-size: $hui-form-field-font-size;
				line-height: $hui-form-field-line-height;
				font-family: $hui-form-field-font-family;
				font-weight: $hui-form-field-font-weight;

				.hustle-modal-optin_icon {
					width: 26px;
					height: 16px;
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: center;
					position: relative;
					margin-left: -26px;

					.hustle-icon {
						display: block;
						flex: 0 0 auto;
						margin: 0 auto;
					}

					.hustle-i_warning {
						width: 14px;
						height: 14px;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%,-50%);
					}
				}

				.hustle-modal-optin_animated {
					opacity: 0;
					transform: translateX(-20px);
					transition: transform 0.3s, opacity 0.35s;
					transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
				}

				.hustle-modal-optin_placeholder {
					opacity: 1;
					overflow: hidden;
					display: block;
					flex: 1;
					text-overflow: ellipsis;
					white-space: nowrap;
					transform: translateX(0);
					transition: $hui-transition;
					transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
				}
			}

			// ICON: STATIC
			&.hustle-modal-field_with_icon {

				input {
					padding-left: 26px;
				}

				label {
					align-items: center;
					padding-left: 26px;
				}

				// HAS ERROR
				input.hustle-modal-optin_error {

					+ label {

						.hustle-modal-optin_icon {

							.hustle-icon {
								opacity: 0;
							}

							.hustle-i_warning {
								opacity: 1;
							}
						}
					}

					&:focus + label {

						.hustle-modal-optin_icon {

							.hustle-icon {
								opacity: 1;
							}

							.hustle-i_warning {
								opacity: 0;
							}
						}
					}
				}

				// IS FILLED
				&.hustle-input-filled {

					.hustle-modal-optin_placeholder {
						display: none;
					}

					input + label,
					input.hustle-modal-optin_error + label {

						.hustle-modal-optin_animated {
							opacity: 1;
							transform: translateX(0);
							transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
						}

						.hustle-modal-optin_placeholder {
							opacity: 0;
							transform: translateX(100px);
							transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
						}
					}
				}
			}

			// IS FILLED
			&.hustle-input-filled {

				.hustle-modal-optin_placeholder {
					display: none;
				}
			}
		}

		&_button {
			display: block;

			button {
				@extend %hui-button;
				width: 100%;
				display: block;
				&:disabled {
					opacity: .3;
					cursor: default;
				}

				.hustle-loading-icon {
					display: none;
				}
			}

			button.hustle-onload {
				@include opacity(0.7);
				pointer-events: none;
				display: flex;
				align-items: center;
				justify-content: center;
				position: relative;

				.hustle-loading-text {
					@include opacity(0);
					pointer-events: none;
				}

				.hustle-loading-icon {
					@extend %hui-loading;
					display: block;
					flex: 1;
					position: absolute;
				}
			}
		}

		&_group {

			> .hustle-modal-mc_groups {
				padding: $hui-gutter-sm;
				margin-bottom: $hui-fields-joined;
				background-color: $hui-mailchimp-background-color;
			}
		}

		&_separated {

			.hustle-modal-optin {

				&_field {
					margin-bottom: $hui-fields-separated;
				}

				&_group {

					> .hustle-modal-mc_groups {
						margin-bottom: $hui-fields-separated;
					}
				}
			}

			.hustle-modal-mc {

				&_title {
					margin-bottom: $hui-fields-separated;
				}
			}
		}
	}

	.hustle-modal-mc {

		&_title {
			display: block;
			margin-bottom: $hui-fields-joined;

			label {
				display: block;
				margin: 0;
				padding: 0;
				border: 0;
				font-size: $hui-mailchimp-title-font-size;
				line-height: $hui-mailchimp-title-line-height;
				font-family: $hui-mailchimp-title-font-family;
				font-weight: $hui-mailchimp-title-font-weight;
			}
		}

		&_groups {
			display: block;

			.select2 {

				&, span {
					display: block;
				}

				.select2-selection {
					height: auto;
					border: 0;
					border-radius: 0;
				}

				.select2-selection--single {
					position: relative;

					.select2-selection__rendered {
						height: $hui-form-field-height;
						padding: $hui-form-field-padding #{($hui-form-field-height - $hui-form-field-border-width) + ($hui-form-field-padding / 2)} $hui-form-field-padding $hui-form-field-padding;
						border-width: $hui-form-field-border-width;
						border-style: $hui-form-field-border-style;
						border-color: transparent;
						line-height: $hui-form-field-line-height;
					}

					.select2-selection__arrow {
						width: #{$hui-form-field-height - $hui-form-field-border-width};
						height: #{$hui-form-field-height - ($hui-form-field-border-width * 2)};
						display: flex;
						align-items: center;
						justify-content: center;
						position: absolute;
						top: 1px;
						right: 1px;

						b {
							display: none;
						}

						&:before {
							content: "\e90a";
							display: block;
							font-size: 16px;
							font-family: "wpdui-fi" !important;
						}
					}
				}

				&.select2-container--open {

					.select2-selection--single {

						.select2-selection__arrow:before {
							transform: rotate(180deg);
						}
					}
				}
			}
		}

		&_option {
			display: inline-flex;
			margin: #{$hui-gutter-sm / 2} 0;
			padding: 0 #{$hui-gutter-sm / 2};
			
			.hustle-modal-mc_radio,
			.hustle-modal-mc_checkbox {
				flex: 0 0 auto;

				input {
					@extend %screen-reader-only;

					&:checked + label {

						&:before {
							@include opacity(1);
						}
					}
				}

				label {
					@extend %hui-checkbox;
					margin: 0;
					padding: 0;
					border: 0;

					&:before {
						@include opacity(0);
					}
				}

				+ .hustle-modal-mc_label {
					margin-left: 6px;
				}
			}

			.hustle-modal-mc_radio {
				
				label {
					border-radius: 50%;
				}
			}

			.hustle-modal-mc_label {
				flex: 0 1 auto;

				label {
					margin: 0;
					padding: 0;
					border: 0;
					font-size: $hui-mailchimp-label-font-size;
					line-height: $hui-mailchimp-label-line-height;
					font-family: $hui-mailchimp-label-font-family;
					font-weight: $hui-mailchimp-label-font-weight;
				}
			}

			&:first-child {
				margin-left: 0;
			}
		}
	}
}

body > .select2-container--open {
	z-index: 999991;
}
