.wpmudev-ui {

	.wpmudev-modal:not(#forminator-popup) {
		width: 100%;
		height: 100%;
		display: none;
		justify-content: center;
		position: fixed;
		z-index: 99999;
		top: 0;
		left: 0;

		.wpmudev-modal-mask {
			width: 100%;
			height: 100%;
			position: fixed;
			top: 0;
			left: 0;
			background-color: $hui-modal-mask-color;
		}

		.wpmudev-box-modal {
			width: 100%;
			max-width: 800px;
			margin: auto 0;
			padding: $hui-gutter-sm 0;
			transform-origin: center;
			transform-style: preserve-3d;
			animation-duration: 1s;
			animation-fill-mode: both;

			&.wpmudev-show {
				opacity: 1;
				animation-name: fadeInUp;
			}
	
			&.wpmudev-hide {
				opacity: 0;
				animation-name: fadeOutDown;
			}

			@include screen("small") {
				padding: $hui-gutter 0;
			}
		}

		// IF active modal
		&.wpmudev-modal-active {
			display: flex;
		}

		// Upsell
		&.wpmudev-modal-upsell {
		
			.wpmudev-box-modal {
				max-width: 600px;
			}
		}
	}

	.wpmudev-modal:not(#forminator-popup):not(.hui-module-type--slidein) {
		overflow-y: auto;
		padding: 0 $hui-gutter-sm;

		// Hustle module
		.hustle-modal {
			margin: auto 0;
			padding: $hui-gutter-sm 0;

			@include screen("small") {
				padding: $hui-gutter 0;
			}
		}

		@include screen("small") {
			padding: 0 $hui-gutter;
		}
	}

	.wpmudev-modal.hui-module-type--slidein:not(#forminator-popup) {
		align-items: center;
		justify-content: center;

		.hustle-modal {
			overflow-y: auto;
		}

		// POSITIONATE MODULE
		&.inc_opt_slidein {

			// (N) North
			// Position X: Center
			// Position Y: Top
			&_n {
				align-items: flex-start;
				padding: 0 $hui-gutter-sm;
			
				.hustle-modal {
					max-height: calc(100vh - #{$hui-gutter-sm});

					@include screen("small") {
						max-height: calc(100vh - #{$hui-gutter});
					}
				}

				@include screen("small") {
					padding: 0 $hui-gutter;
				}
			}

			// (S) SOUTH
			// Position X: Center
			// Position Y: Bottom
			&_s {
				align-items: flex-end;
				padding: 0 $hui-gutter-sm;

				.hustle-modal {
					max-height: calc(100vh - #{$hui-gutter-sm});

					@include screen("small") {
						max-height: calc(100vh - #{$hui-gutter});
					}
				}

				@include screen("small") {
					padding: 0 $hui-gutter;
				}
			}

			// (E) EAST
			// Position X: Right
			// Position Y: Center
			&_e {
				justify-content: flex-end;
				padding-left: $hui-gutter-sm;

				.hustle-modal {
					max-height: calc(100vh - #{$hui-gutter-sm * 2});

					@include screen("small") {
						max-height: calc(100vh - #{$hui-gutter * 2});
					}
				}

				@include screen("small") {
					padding-left: $hui-gutter;
				}
			}

			// (W) WEST
			// Position X: Left
			// Position Y: Center
			&_w {
				justify-content: flex-start;
				padding-right: $hui-gutter-sm;

				.hustle-modal {
					max-height: calc(100vh - #{$hui-gutter-sm * 2});

					@include screen("small") {
						max-height: calc(100vh - #{$hui-gutter * 2});
					}
				}

				@include screen("small") {
					padding-right: $hui-gutter;
				}
			}

			// (NE) NORTH EAST
			// Position X: Right
			// Position Y: Top
			&_ne {
				justify-content: flex-end;
				align-items: flex-start;
				padding-left: $hui-gutter-sm;

				.hustle-modal {
					max-height: calc(100vh - #{$hui-gutter-sm});

					@include screen("small") {
						max-height: calc(100vh - #{$hui-gutter});
					}
				}

				@include screen("small") {
					padding-left: $hui-gutter;
				}
			}

			// (SE) SOUTH EAST
			// Position X: Right
			// Position Y: Bottom
			&_se {
				justify-content: flex-end;
				align-items: flex-end;
				padding-left: $hui-gutter-sm;

				.hustle-modal {
					max-height: calc(100vh - #{$hui-gutter-sm});

					@include screen("small") {
						max-height: calc(100vh - #{$hui-gutter});
					}
				}

				@include screen("small") {
					padding-left: $hui-gutter;
				}
			}

			// (NW) NORTH WEST
			// Position X: Left
			// Position Y: Top
			&_nw {
				justify-content: flex-start;
				align-items: flex-start;
				padding-right: $hui-gutter-sm;

				.hustle-modal {
					max-height: calc(100vh - #{$hui-gutter-sm});

					@include screen("small") {
						max-height: calc(100vh - #{$hui-gutter});
					}
				}

				@include screen("small") {
					padding-right: $hui-gutter;
				}
			}

			// (SW) SOUTH WEST
			// Position X: Left
			// Position y: Bottom
			&_sw {
				justify-content: flex-start;
				align-items: flex-end;
				padding-right: $hui-gutter-sm;

				.hustle-modal {
					max-height: calc(100vh - #{$hui-gutter-sm});

					@include screen("small") {
						max-height: calc(100vh - #{$hui-gutter});
					}
				}

				@include screen("small") {
					padding-right: $hui-gutter;
				}
			}
		}
	}
}

// Prevent content scroll when modal is active
body.wp-admin {
	
	&.wpmudev-modal-is_active {
		overflow: hidden;
	}
}