%hui-image-fit-fill {

	img {
		width: 100%;
		height: 100%;
		display: block;
		position: absolute;
		object-fit: fill;
		-ms-interpolation-mode: bicubic;
	}
}

%hui-image-fit-contain {

	img {
		width: 100%;
		height: 100%;
		display: block;
		position: absolute;
		object-fit: contain;
		-ms-interpolation-mode: bicubic;

		@include support(ie) {
			width: auto;
			max-width: 100%;
			height: auto;
			max-height: 100%;
		}
	}
}

%hui-image-fit-cover {

	img {
		width: 100%;
		height: 100%;
		display: block;
		position: absolute;
		object-fit: cover;
		-ms-interpolation-mode: bicubic;

		@include support(ie) {
			height: auto;
		}
	}
}

%hui-image-fit-full {
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: center;

	img {
		width: auto;
		max-width: 100%;
		height: auto;
		display: block;
		flex: 0 1 auto;
		margin: 0 auto;
	}
}

.hustle-modal {

	.hustle-modal-image {
		position: relative;

		// IMAGE Position
		&.hustle-modal-image_cover img,
		&.hustle-modal-image_contain img {

			&.hustle-modal-image_lefttop {
				object-position: left top;

				@include support(ie) {
					top: 0;
					left: 0;
				}
			}

			&.hustle-modal-image_leftcenter {
				object-position: left center;

				@include support(ie) {
					top: 50%;
					transform: translate(0,-50%);
				}
			}

			&.hustle-modal-image_leftbottom {
				object-position: left bottom;

				@include support(ie) {
					bottom: 0;
				}
			}

			&.hustle-modal-image_centertop {
				object-position: center top;

				@include support(ie) {
					left: 50%;
					transform: translate(-50%,0);
				}
			}

			&.hustle-modal-image_centercenter {
				object-position: center;

				@include support(ie) {
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
				}
			}

			&.hustle-modal-image_centerbottom {
				object-position: center bottom;

				@include support(ie) {
					left: 50%;
					bottom: 0;
					transform: translate(-50%,0);
				}
			}

			&.hustle-modal-image_righttop {
				object-position: right top;

				@include support(ie) {
					top: 0;
					right: 0;
				}
			}

			&.hustle-modal-image_rightcenter {
				object-position: right center;

				@include support(ie) {
					right: 0;
					top: 50%;
					transform: translate(0,-50%);
				}
			}

			&.hustle-modal-image_rightbottom {
				object-position: right bottom;

				@include support(ie) {
					right: 0;
					bottom: 0;
				}
			}

			@include support(ie) {

				&.hustle-modal-image_centercustom {
					left: 50%;
					transform: translate(-50%,0);
				}

				&.hustle-modal-image_customcenter {
					top: 50%;
					transform: translate(0,-50%);
				}
			}
		}

		&.hustle-modal-mobile_hidden {
	
			@media (max-width: 782px) {
				display: none;
			}
		}
	}
}