.hustle-modal-four {

	.hustle-modal-body {

		aside {

			.hustle-modal-image {
	
				&_fill,
				&_cover,
				&_contain {
					height: $hui-image-height-sm;
					overflow: hidden;
				}
		
				&_fill {
					@extend %hui-image-fit-fill;
				}
		
				&_contain {
					@extend %hui-image-fit-contain;
				}
		
				&_cover {
					@extend %hui-image-fit-cover;
				}
		
				&_none {
					@extend %hui-image-fit-full;
				}
			}

			.hustle-modal-optin_wrap {
				padding: $hui-gutter-sm;

				form + .hustle-gdpr-box {
					margin-top: $hui-gutter-sm;
				}
			}
		}

		section {
			margin: 0;
			padding: $hui-gutter-sm;
			border: 0;
		}

		&:not(.hustle-size-small) {

			aside {
	
				.hustle-modal-optin_wrap {
	
					form + .hustle-gdpr-box {
	
						@include screen("small") {
							margin-top: $hui-gutter;
						}
					}
	
					.hui-modal-footer-wrap {
	
						@include screen("small") {
							flex: 1;	
							margin: auto 0;
						}
					}
	
					@include screen("small") {
						display: flex;
						flex: 1;
						align-items: center;
						padding: $hui-gutter;
					}
				}
	
				&.hui-modal-sidebar {
	
					&-fill,
					&-cover,
					&-contain {

						&:only-child {

							@include screen("small") {
								flex: 1;
							}
						}
	
						@include screen("small") {
							flex: 0 0 $hui-image-height;
						}
					}
	
					&-none {

						&:only-child {

							@include screen("small") {
								max-width: unset;
								flex: 1;
							}
						}
					
						@include screen("small") {
							max-width: 50%;
							flex: 0 0 auto;
						}
					}
				}
	
				@include screen("small") {
					display: flex;
					flex-direction: column;

					@include support(ie) {
						display: block;
					}
				}
			}

			section {
	
				article {
	
					@include screen("small") {
						flex: 1;
						margin: auto 0;
					}
				}
	
				@include screen("small") {
					display: flex;
					flex: 1;
					align-items: center;
					padding: $hui-gutter;
				}
			}

			@include screen("small") {
				display: flex;
			}
		}
	}
}