.hustle-modal-two {

	.hustle-modal {

		&-image {

			&_fill,
			&_cover,
			&_contain {
				height: $hui-image-height-sm;
				overflow: hidden;
			}
	
			&_fill {
				@extend %hui-image-fit-fill;
			}
	
			&_contain {
				@extend %hui-image-fit-contain;
			}
	
			&_cover {
				@extend %hui-image-fit-cover;
			}
	
			&_none {
				@extend %hui-image-fit-full;
			}
		}

		&-content {

			section {
				padding: $hui-gutter-sm;
			}
	
			footer {
				margin: 0;
				padding: $hui-gutter-sm;
				border: 0;
	
				form + .hustle-gdpr-box {
					margin-top: $hui-gutter-sm;
				}
			}
		}

		&-optin_form {
			margin: 0;
			padding: 0;
			border: 0;
		}
	}

	.hustle-modal-body:not(.hustle-size-small) {

		.hustle-modal {

			&-image {

				&_fill,
				&_cover,
				&_contain {
		
					@include screen("small") {
						max-width: $hui-image-height;
						height: auto;
						flex: 0 0 $hui-image-height;
					}
				}
		
				&_none {
		
					@include screen("small") {
						max-width: 50%;
						flex: 0 0 auto;
					}
				}
			}

			&-content {

				section {
	
					@include screen("small") {
						padding: $hui-gutter;
					}
				}
		
				footer {
		
					form + .hustle-gdpr-box {
	
						@include screen("small") {
							margin-top: $hui-gutter;
						}
					}
	
					@include screen("small") {
						padding: $hui-gutter;
					}
				}
	
				@include screen("small") {
					display: flex;
					flex: 1;
					flex-direction: column;
					justify-content: center;
				}
			}

			&-optin_form {
	
				@include screen("small") {
					display: flex;
					align-items: center;
				}
			}

			&-optin_field {

				&:last-child {
					
					@include screen("small") {
						margin-right: 0;
					}
				}
	
				@include screen("small") {
					flex: 1;
					margin-right: $hui-fields-joined;
					margin-bottom: 0;
				}
			}

			&-optin_groups {

				> .hustle-modal-optin_group {
					
					@include screen("small") {
						display: flex;
						flex-wrap: wrap;
					}
				}
				
				@include screen("small") {
					display: block;
				}
			}

			&-optin_group {

				> .hustle-modal-mc_groups {
	
					@include screen("small") {
						min-width: 100%;
						flex: 0 0 100%;
					}
				}
	
				.hustle-modal-optin_button {
	
					@include screen("small") {
						min-width: 100%;
						flex: 0 0 100%;
					}
				}
			}

			&-optin_button {

				button {
	
					@include screen("small") {
						min-width: 100px;
					}
				}
			}

			&-optin_separated {

				.hustle-modal-optin {
	
					&_field {
	
						&:last-child {
							
							@include screen("small") {
								margin-right: 0;
							}
						}
	
						@include screen("small") {
							margin-right: $hui-fields-separated;
							margin-bottom: 0;
						}
					}
				}
			}
		}

		@include screen("small") {
			display: flex;
		}
	}
}