.hustle-modal-one {

	section {

		&.hustle-modal-image_left,
		&.hustle-modal-image_right {

			.hustle-modal-image {

				&_fill,
				&_cover,
				&_contain {
					height: $hui-image-height-sm;
					overflow: hidden;
				}
			}
		}

		&.hustle-modal-image_above,
		&.hustle-modal-image_below {

			.hustle-modal-image {

				&_fill,
				&_cover,
				&_contain {
					height: $hui-image-height-sm;
					overflow: hidden;
				}
			}
		}

		.hustle-modal-image {

			&_fill {
				@extend %hui-image-fit-fill;
			}

			&_contain {
				@extend %hui-image-fit-contain;
			}

			&_cover {
				@extend %hui-image-fit-cover;
			}

			&_none {
				@extend %hui-image-fit-full;
			}
		}

		article {

			.hustle-modal-article {
				padding: $hui-gutter-sm;
			}
		}
	}

	footer {
		position: relative;
		margin: 0;
		padding: $hui-gutter-sm;
		border: 0;

		.hustle-modal-optin {

			&_form {
				margin: 0;
				padding: 0;
				border: 0;

				+ .hustle-gdpr-box {
					margin-top: $hui-gutter-sm;
				}
			}
		}
	}

	.hustle-modal-body:not(.hustle-size-small) {

		section {

			&.hustle-modal-image_left,
			&.hustle-modal-image_right {

				.hustle-modal-image {

					&_fill,
					&_cover,
					&_contain {

						&:only-child {
							
							@include screen("small") {
								max-width: unset;
								height: $hui-image-height;
								flex: 1;
							}
						}
	
						@include screen("small") {
							max-width: $hui-image-height;
							height: auto;
							flex: 0 0 $hui-image-height;
						}
					}
	
					&_none {

						&:only-child {
							
							@include screen("small") {
								max-width: unset;
								flex: 1;
							}
						}
	
						@include screen("small") {
							max-width: 50%;
							flex: 0 0 auto;
						}
					}
				}

				@include screen("small") {
					display: flex;
				}
			}

			article {

				.hustle-modal-article {
	
					@include screen("small") {
						padding: $hui-gutter;
					}
				}
			}
		}

		footer {
			
			.hustle-modal-optin {

				&_form {
	
					+ .hustle-gdpr-box {
	
						@include screen("small") {
							margin-top: $hui-gutter;
						}
					}
	
					@include screen("small") {
						display: flex;
						align-items: center;
					}
				}

				&_field {

					&:last-child {
					
						@include screen("small") {
							margin-right: 0;
						}
					}
		
					@include screen("small") {
						flex: 1;
						margin-right: $hui-fields-joined;
						margin-bottom: 0;
					}
				}

				&_button {

					button {
	
						@include screen("small") {
							min-width: 100px;
						}
					}
				}

				&_groups {

					> .hustle-modal-optin_group {
						
						@include screen("small") {
							display: flex;
							flex-wrap: wrap;
						}
					}
					
					@include screen("small") {
						display: block;
					}
				}

				&_group {

					> .hustle-modal-mc_groups {
	
						@include screen("small") {
							min-width: 100%;
							flex: 0 0 100%;
						}
					}
	
					.hustle-modal-optin_button {
	
						@include screen("small") {
							min-width: 100%;
							flex: 0 0 100%;
						}
					}
				}

				&_separated {

					.hustle-modal-optin {
	
						&_field {
	
							&:last-child {
								
								@include screen("small") {
									margin-right: 0;
								}
							}
	
							@include screen("small") {
								margin-right: $hui-fields-separated;
								margin-bottom: 0;
							}
						}
					}
				}
			}

			@include screen("small") {
				padding: $hui-gutter;
			}
		}
	}
}