.hustle-modal-minimal {

	.hustle-modal {

		&-body {
			overflow: hidden;
		}

		&-image {

			&_fill,
			&_cover,
			&_contain {
				height: $hui-image-height-sm;
			}
	
			&_fill {
				@extend %hui-image-fit-fill;
			}
	
			&_contain {
				@extend %hui-image-fit-contain;
			}
	
			&_cover {
				@extend %hui-image-fit-cover;
			}
	
			&_none {
				@extend %hui-image-fit-full;
			}
		}

		&-header {
			padding: $hui-gutter-sm;

			+ .hustle-modal-content {
				border-top-width: $hui-module-border-width;
				border-top-style: $hui-module-border-style;
				border-top-color: $hui-module-border-color;
			}
		}

		&-message {
			padding: $hui-gutter-sm;
		}

		&-footer {
			padding: $hui-gutter-sm;
			background-color: $hui-module-footer-background;

			.hustle-gdpr-box {

				+ .hustle-modal-cta {
					margin-top: $hui-gutter-sm;
				}
			}

			.hustle-modal-cta {
				display: block;
			}
		}
	}

	.hustle-modal-body:not(.hustle-size-small) {

		.hustle-modal {

			&-header {

				@include screen("small") {
					padding: $hui-gutter;
				}
			}

			&-content {
				
				@include screen("small") {
					display: flex;
				}
			}

			&-image {

				&_fill,
				&_cover,
				&_contain {

					&:only-child {

						@include screen("small") {
							max-width: 100%;
							height: $hui-image-height;
							flex: 1;
						}
					}
		
					@include screen("small") {
						max-width: $hui-image-height;
						height: auto;
						flex: 0 0 $hui-image-height;
					}
				}
		
				&_none {

					&:only-child {

						@include screen("small") {
							max-width: 100%;
							flex: 1;
						}
					}
		
					@include screen("small") {
						max-width: 50%;
						flex: 0 0 auto;
					}
				}
			}

			&-wrap--content {

				@include screen("small") {
					display: flex;
					flex: 1;
					align-items: center;
					padding: $hui-gutter;
				}
			}

			&-message {

				@include screen("small") {
					flex: 1;
					margin: auto 0;
					padding: 0;
				}
			}

			&-footer {

				.hustle-gdpr-box {

					+ .hustle-modal-cta {
						
						@include screen("small") {
							margin-top: 0;
							margin-left: $hui-gutter;
						}
					}

					@include screen("small") {
						flex: 1;
					}
				}

				.hustle-modal-cta {

					@include screen("small") {
						display: inline-block;
						flex: 0 0 auto;
					}
				}

				@include screen("small") {
					display: flex;
					padding: $hui-gutter;
				}
			}
		}
	}
}