.hustle-modal {
	width: 100%;
	max-width: 800px;
	opacity: 0;
	position: relative;

	* {
		box-sizing: border-box;
		text-rendering: geometricPrecision;
		-webkit-font-smoothing: antialiased;
	}

	&.hustle-modal-image_only {
		width: auto;
		max-width: 100%;
	}

	&.hustle-modal-static {
		opacity: 1;
	}
}

// PAGE SCROLL DISABLED
html.hustle-no-scroll body {
	overflow: hidden;
}