.hustle-modal {

	.hustle-modal-success {
		width: 100%;
		visibility: hidden;
		@include opacity(0);
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		padding: $hui-gutter-sm;
		transition: $hui-transition;

		.hustle-modal-success_icon {
			display: block;

			.hustle-icon {
				width: 30px;
				height: 22px;
				display: block;
				margin: 0 auto;
			}

			+ .hustle-modal-success_message {
				margin-top: $hui-gutter-sm;

				@include screen("small") {
					margin-top: $hui-gutter;
				}
			}
		}

		.hustle-modal-success_message {
			overflow-y: auto;
		}

		&.hustle-modal-success_show {
			visibility: visible;
			@include opacity(1);
		}

		@include screen("small") {
			padding: $hui-gutter;
		}
	}
}

.wph-modal,
#wph-preview-modal {

	&.hui-module-type--popup {

		.hustle-modal {

			.hustle-modal-success {
				animation-duration: 1s;
				animation-fill-mode: both;
				transform-origin: center;
				transform-style: preserve-3d;
				top: #{$hui-gutter-sm + $hui-close-size};

				&.hustle-modal-success_show {
					animation-name: slideInUp;
				}

				@include screen("small") {
					top: #{$hui-gutter + $hui-close-size};
				}
			}
		}
	}

	&.hui-module-type--slidein {

		.hustle-modal {

			.hustle-modal-success {
				top: $hui-close-size;
			}
		}
	}
}