// Load modules
@import "modules/modules";

@mixin opacity($alpha: 1) {
	filter: alpha(opacity=$alpha*100);
	filter: progid:DXImageTransform.Microsoft.Alpha(opacity=#{$alpha*100});
	opacity: $alpha;
	-khtml-opacity: $alpha;
}

@mixin support($support-type) {

	@if $support-type == ie {

		@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
			@content;
		}
	}

	@else if $support-type == edge {

		@supports (-ms-ime-align: auto) {
			@content;
		}
	}

	@else {

		@warn "Unfortunately, no type could be retrieved from `#{$support-type}`. "
		+ "Use either `ie` or `edge`.";
	}
}

$hui-gutter: 20px;
$hui-gutter-sm: 10px;

$hui-transition: 0.3s ease;

$hui-close-size: 30px;

$hui-image-height: 320px;
$hui-image-height-sm: 150px;

$hui-module-title-font-size: 33px;
$hui-module-title-line-height: 38px;
$hui-module-title-font-family: 'Georgia', serif;
$hui-module-title-font-weight: 400;

$hui-module-subtitle-font-size: 14px;
$hui-module-subtitle-line-height: 24px;
$hui-module-subtitle-font-family: $font--opn;
$hui-module-subtitle-font-weight: bold;

$hui-module-border-width: 1px;
$hui-module-border-style: solid;
$hui-module-border-color: rgba(0, 0, 0, 0.16);
$hui-module-box-shadow: 0 $hui-module-border-width 0 rgba(255, 255, 255, 0.08);
$hui-module-footer-background: rgba(0, 0, 0, 0.16);

$hui-optin-title-font-size: 20px;
$hui-optin-title-line-height: 27px;
$hui-optin-title-font-family: $font--opn;
$hui-optin-title-font-weight: bold;

$hui-optin-subtitle-font-size: 14px;
$hui-optin-subtitle-line-height: 18px;
$hui-optin-subtitle-font-family: $font--opn;
$hui-optin-subtitle-font-weight: bold;

$hui-fields-joined: 1px;
$hui-fields-separated: 10px;

$hui-form-field-height: 40px;
$hui-form-field-padding: 10px;
$hui-form-field-border-width: 1px;
$hui-form-field-border-style: solid;
$hui-form-field-font-size: 13px;
$hui-form-field-line-height: #{$hui-form-field-height - ($hui-form-field-padding * 2) - ($hui-form-field-border-width * 2)};
$hui-form-field-font-weight: 300;
$hui-form-field-font-family: $font--opn;

$hui-checkbox-size: 20px;
$hui-checkbox-icon-size: 12px;
$hui-checkbox-border-width: $hui-form-field-border-width;
$hui-checkbox-border-style: $hui-form-field-border-style;

$hui-mailchimp-background-color: rgba(0, 0, 0, 0.1);

$hui-mailchimp-title-font-size: 15px;
$hui-mailchimp-title-line-height: 28px;
$hui-mailchimp-title-font-family: $font--opn;
$hui-mailchimp-title-font-weight: 600;

$hui-mailchimp-label-font-size: 14px;
$hui-mailchimp-label-line-height: $hui-checkbox-size;
$hui-mailchimp-label-font-family: $font--opn;
$hui-mailchimp-label-font-weight: 300;

$hui-gdpr-font-size: 12px;
$hui-gdpr-line-height: $hui-checkbox-size;
$hui-gdpr-font-family: $font--opn;
$hui-gdpr-font-weight: 400;

$hui-button-size: 40px;
$hui-button-font-size: 14px;
$hui-button-line-height: 24px;
$hui-button-font-family: $font--opn;
$hui-button-font-weight: bold;

%hui-checkbox {
	width: $hui-checkbox-size;
	height: $hui-checkbox-size;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0;
	padding: 0;
	border-width: $hui-checkbox-border-width;
	border-style: $hui-checkbox-border-style;
	border-color: transparent;
	transition: $hui-transition;

	&:before {
		display: block;
		color: inherit;
		font-size: $hui-checkbox-icon-size;
		line-height: $hui-checkbox-size;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-rendering: optimizeLegibility;
		text-transform: none;
	}
}

%hui-button {
	width: auto;
	min-width: 100px;
	display: inline-block;
	margin: 0;
	padding: #{($hui-button-size - $hui-button-line-height) / 2} #{$hui-button-size - $hui-button-line-height};
	border: 0;
	border-radius: 0;
	color: $color-white;
	font-size: $hui-button-font-size;
	line-height: $hui-button-line-height;
	font-family: $hui-button-font-family;
	font-weight: $hui-button-font-weight;
	text-align: center;
	transition: $hui-transition;

	&,
	&:hover,
	&:active {
		outline: none;
		box-shadow: none;
		text-decoration: none;
	}
}

%hui-loading {
	width: $base-line-height;
	height: $base-line-height;
	display: block;
	margin: 0 auto;
	border: 2px solid $off-darkgray;
	border-top-color: $color-gray--light;
	border-radius: 50%;
	animation: spin $spin-duration infinite linear;
}



