.hustle-modal-simple,
.hustle-modal-minimal,
.hustle-modal-cabriolet {

	.hustle-modal-header {

		.hustle-modal-title,
		.hustle-modal-subtitle {
			margin: 0;
			padding: 0;
			border: 0;
			letter-spacing: 0;
			text-transform: none;
		}

		.hustle-modal-title {
			font-size: $hui-module-title-font-size;
			line-height: $hui-module-title-line-height;
			font-family: $hui-module-title-font-family;
			font-weight: $hui-module-title-font-weight;
			
			+ .hustle-modal-subtitle {
				margin: $hui-gutter-sm 0 0;
			}
		}

		.hustle-modal-subtitle {
			font-size: $hui-module-subtitle-font-size;
			line-height: $hui-module-subtitle-line-height;
			font-family: $hui-module-subtitle-font-family;
			font-weight: $hui-module-subtitle-font-weight;
		}
	}
}