.hustle-modal .hustle-gdpr-box {
	display: flex;

	.hustle-gdpr-checkbox {
		flex: 0 0 20px;
		align-self: flex-start;
		margin: 0 10px 0 0;
		padding: 0;
		border: 0;

		input {
			@extend %screen-reader-only;

			&:checked + span {
				border-color: transparent;

				&:before {
					@include opacity(1);
				}

				&.hustle-modal-optin_error {
					border-color: transparent;
				}
			}
		}

		span {
			@extend %hui-checkbox;
			border: 2px solid transparent;
			background-color: $color-white; // SAMPLE ONLY
			color: $color-green; // SAMPLE ONLY
			transition: 0.3s ease;

			&.hustle-modal-optin_error {
				border-color: $color-red;
			}
			
			&:before {
				content: "\e925";
				@include opacity(0);
				font-family: "wpdui-fi" !important;
			}

			&.hustle-modal-optin_error {
				border-color: $color-red; // SAMPLE ONLY
			}
		}
	}

	.hustle-gdpr-content {
		flex: 0 1 auto;

		* {
			color: inherit;

			&:first-child {
				margin-top: 0;
				padding-top: 0;
			}

			&:last-child {
				margin-bottom: 0;
				padding-bottom: 0;
			}
		}
		
		&, p {
			font-size: $hui-gdpr-font-size;
			line-height: $hui-gdpr-line-height;
			font-family: $hui-gdpr-font-family;
			font-weight: $hui-gdpr-font-weight;
		}

		@include support(ie) {
			flex: 1;
		}
	}

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}
}
