.wph-modal {

	&.hui-module-type--popup {
		width: 100%;
		height: 100%;
		overflow-y: auto;
		display: none;
		align-items: center;
		justify-content: center;
		position: fixed;
		z-index: 999991;
		top: 0;
		left: 0;
		padding: 0 $hui-gutter-sm;

		.wph-modal-mask {
			width: 100%;
			height: 100%;
			position: fixed;
			top: 0;
			left: 0;
		}

		.hustle-modal {
			margin: auto 0;
			padding: $hui-gutter-sm 0;

			@include screen("small") {
				padding: $hui-gutter 0;
			}
		}
		
		&.wph-modal-active {
			display: flex;
		}

		@include support(ie) {
			align-items: baseline;
		}

		@include screen("small") {
			padding: 0 $hui-gutter;
		}
	}

	&.hui-module-type--slidein {
		width: 100%;
		height: 100%;
		pointer-events: none;
		display: none;
		align-items: center;
		justify-content: center;
		position: fixed;
		z-index: 999991;
		top: 0;
		left: 0;

		.hustle-modal {
			overflow-y: auto;
			pointer-events: initial;
		}

		&.wph-modal-active {
			display: flex;
		}
	}

	// SLIDEIN positionate module
	&.inc_opt_slidein {

		// (N) North
		// Position X: Center
		// Position Y: Top
		&_n {
			align-items: flex-start;
			padding: 0 $hui-gutter-sm;

			.hustle-modal {
				max-height: calc(100vh - #{$hui-gutter-sm});

				@include screen("small") {
					max-height: calc(100vh - #{$hui-gutter});
				}
			}

			@include screen("small") {
				padding: 0 $hui-gutter;
			}
		}

		// (S) SOUTH
		// Position X: Center
		// Position Y: Bottom
		&_s {
			align-items: flex-end;
			padding: 0 $hui-gutter-sm;

			.hustle-modal {
				max-height: calc(100vh - #{$hui-gutter-sm});

				@include screen("small") {
					max-height: calc(100vh - #{$hui-gutter});
				}
			}

			@include screen("small") {
				padding: 0 $hui-gutter;
			}
		}

		// (E) EAST
		// Position X: Right
		// Position Y: Center
		&_e {
			justify-content: flex-end;
			padding-left: $hui-gutter-sm;

			.hustle-modal {
				max-height: calc(100vh - #{$hui-gutter-sm * 2});

				@include screen("small") {
					max-height: calc(100vh - #{$hui-gutter * 2});
				}
			}

			@include screen("small") {
				padding-left: $hui-gutter;
			}
		}

		// (W) WEST
		// Position X: Left
		// Position Y: Center
		&_w {
			justify-content: flex-start;
			padding-right: $hui-gutter-sm;

			.hustle-modal {
				max-height: calc(100vh - #{$hui-gutter-sm * 2});

				@include screen("small") {
					max-height: calc(100vh - #{$hui-gutter * 2});
				}
			}

			@include screen("small") {
				padding-right: $hui-gutter;
			}
		}

		// (NE) NORTH EAST
		// Position X: Right
		// Position Y: Top
		&_ne {
			justify-content: flex-end;
			align-items: flex-start;
			padding-left: $hui-gutter-sm;

			.hustle-modal {
				max-height: calc(100vh - #{$hui-gutter-sm});

				@include screen("small") {
					max-height: calc(100vh - #{$hui-gutter});
				}
			}

			@include screen("small") {
				padding-left: $hui-gutter;
			}
		}

		// (SE) SOUTH EAST
		// Position X: Right
		// Position Y: Bottom
		&_se {
			justify-content: flex-end;
			align-items: flex-end;
			padding-left: $hui-gutter-sm;

			.hustle-modal {
				max-height: calc(100vh - #{$hui-gutter-sm});

				@include screen("small") {
					max-height: calc(100vh - #{$hui-gutter});
				}
			}

			@include screen("small") {
				padding-left: $hui-gutter;
			}
		}

		// (NW) NORTH WEST
		// Position X: Left
		// Position Y: Top
		&_nw {
			justify-content: flex-start;
			align-items: flex-start;
			padding-right: $hui-gutter-sm;

			.hustle-modal {
				max-height: calc(100vh - #{$hui-gutter-sm});

				@include screen("small") {
					max-height: calc(100vh - #{$hui-gutter});
				}
			}

			@include screen("small") {
				padding-right: $hui-gutter;
			}
		}

		// (SW) SOUTH WEST
		// Position X: Left
		// Position y: Bottom
		&_sw {
			justify-content: flex-start;
			align-items: flex-end;
			padding-right: $hui-gutter-sm;

			.hustle-modal {
				max-height: calc(100vh - #{$hui-gutter-sm});

				@include screen("small") {
					max-height: calc(100vh - #{$hui-gutter});
				}
			}

			@include screen("small") {
				padding-right: $hui-gutter;
			}
		}
	}
}

// PREVIEW
// This is a fix for preview modal to give the same
// top and bottom spacing popup module has on front.
.wpmudev-ui #wph-preview-modal {

	&:not(#forminator-popup):not(.hui-module-type--slidein) {

		&.hui-module-type--popup {

			.hustle-modal {
				padding: $hui-gutter-sm 0;
	
				@include screen("small") {
					padding: $hui-gutter 0;
				}
			}
		}
	}
}